import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'

Vue.use(Vuex)

const state = {
	latitude: '', //当前位置纬度
	longitude: '', //当前位置经度
   	userInfo: null, //用户信息
   	geohash: 'wtw3sm0q087',//地址geohash值
   	login: true,  //是否登录
   	imgPath: null, //头像地址
}

export default new Vuex.Store({
	state,
	mutations,
	actions
})



